import { FC } from 'react'
import { useSlider } from 'hooks/useSlider'
import { sliderImages } from 'utils/sliderImages'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle } from '@fortawesome/free-solid-svg-icons'

const Slider: FC = (): JSX.Element => {
  const inicialState = 0
  const restartState: number = sliderImages.length - 1

  const { counter, setCounter } = useSlider({ inicialState, restartState })

  const selectImage = (index: number) => setCounter(index)

  return <div className="sliderBox" title="Slider">
    <img src={sliderImages[counter]} alt="slider" />
    <div className="dotSlider">
      {
        sliderImages.map((img, index) => <FontAwesomeIcon key={index} onClick={() => selectImage(index)} icon={faCircle} className={img === sliderImages[counter] ? 'active' : undefined} />)
      }
    </div>
  </div>
}

export default Slider